<template>
    <div class="card custom">
        <div class="card-body">
            <div class="content">
                <div class="content-title content-menu">
                    <span>Recent Works</span>
                    <ul class="nav">
                        <li class="nav-item">
                            <a href="#" id="All" class="nav-link" @click="filter('All')" >All</a>
                        </li>
                        <li class="nav-item">
                            <a href="#" id="Restrant" class="nav-link" @click="filter('Restrant')">restrant</a>
                        </li>
                        <li class="nav-item">
                            <a href="#" class="nav-link" @click="filter('portfolio')">portfolio</a>
                        </li>
                        <li class="nav-item">
                            <a href="#" class="nav-link" @click="filter('e-commerce')">e-commerce</a>
                        </li>
                    </ul>
                </div>
                <div class="line"></div>
                <div class="row">
                    <div class="col-md-6 myWork" v-for="work in datafilter" :key="work.id">
                        <div class="work mt-3">
                            <div class="image">
                                <span>{{ work.type }}</span>
                                <img :src="work.image" alt="img">
                            </div>
                            <div class="title">
                                <h3>{{ work.title }}</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'WorkView',
    data(){
        return {
            works: [],
            cateNam: ''
        }
    },
    created(){
        axios.get(`https://laser-rare-contraption.glitch.me/works`).then(response => {
            this.works = response.data;
        })
    }, 
    methods: {
        filter(type){
            this.cateNam = type;
        }
    },
    computed: {
        datafilter: function(){
            if(this.cateNam == '' || this.cateNam == "All"){
                return this.works;
            }
            else {
                return this.works.filter(work => {
                    return work.type == this.cateNam;
                })
            }
            // const result = this.works.filter( (work) => {
            //     if(this.cateNam == 'All'){
            //         this.selected = this.cateNam;
            //         return this.work;
            //     }
            //     else if(this.cateNam == 'Restrant'){
            //         this.selected = this.cateNam;
            //         return work.type == this.selected;
            //     }
            // })
        }
    } 
}
</script>

<style>
.content-menu .nav .nav-item .nav-link {
    font-size: 16px;
    color: #333;
    font-weight: normal;
}
.work {
    transition: all 0.5s ease-in-out;
}
.work .image img {
    width: 100%;
    border: 2px solid #ddd;
}
.work .image {
    position: relative;
}
.work .image span {
    position: absolute;
    right: 0;
    padding: 5px 10px;
    background-color: var(--main-color);
    color: #fff;
}
.work .title h3 {
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
}
.work .title {
    text-align: center;
    padding: 10px;
}
</style>