<template>
    <div class="card custom">
        <div class="card-body">
            <div class="content-title">
                <span>Blog</span>
            </div>
            <div class="line"></div>
            <div class="blog">
                <div class="row">
                    <div class="col-md-6 blog-card" v-for="blog in blogs" :key="blog.id">
                        <div class="image">
                            <router-link :to="{name: 'post', force: true , state: {post_id: blog.id, post_img: blog.image, post_title: blog.title, post_tags: blog.tags, post_author: blog.author}}">
                                <img :src="blog.image" alt="img">
                                <span class="info">
                                    <i class="fa fa-file icon"></i>
                                </span>
                            </router-link>
                        </div>
                        <div class="date">
                            <span>{{ blog.date }}</span>
                        </div>
                        <div class="title">
                            <router-link :to="{name: 'post'}">
                                <h3>{{ blog.title }}</h3>
                            </router-link>
                        </div>
                        <div class="desc">
                            {{ blog.desc }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: "blogView",
    data(){
        return {
            blogs: [],
        }
    },
    async created() {
        await axios.get(`https://laser-rare-contraption.glitch.me/blogs`).then(response => {
            this.blogs = response.data;
        })
    }
}
</script>

<style>
.blog {
    margin-top: 30px;
    text-align: center;
}
.blog-card {
    margin-bottom: 30px;
}
.blog .image {
    position: relative;
}
.blog .image img {
    width: 100%;
    border: 2px solid #ddd;
}
.blog .title {
    padding: 5px;
}
.blog .title h3 {
    color: var(--main-color);
    font-size: 20px;
    font-weight: normal;
    font-family: Arial, Helvetica, sans-serif;
}
.blog .date {
    text-align: center;
    margin: 15px;
}
.blog .date span {
    text-align: center;
    padding: 10px;
    border: 2px solid var(--main-color);
    color: var(--main-color);
}
.blog .desc {
    width: 300px;
    overflow: hidden;
}
.blog .image .info {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    opacity: 0;
    text-align: center;
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
}
.blog .image .info:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: -moz-linear-gradient( -45deg, rgba(120, 210, 109, 0.4) 0%, rgba(120, 204, 109, 0.01) 100% );
    background: -webkit-linear-gradient( -45deg, rgba(120, 210, 109, 0.4) 0%, rgba(120, 204, 109, 0.01) 100% );
    background: linear-gradient( 135deg, rgba(120, 210, 109, 0.4) 0%, rgba(120, 204, 109, 0.01) 100% );
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
}
.blog .image .info:hover {
    opacity: 1;
}
.blog .image .icon {
    font-size: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
}
</style>